import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { AuthGuard } from "./guard/auth.guard";
import { RoleTypeSM } from "./models/service/enums/role-type-s-m.enum";

const routes: Routes = [
  {
    path: "",
    redirectTo: "common",
    pathMatch: "full",
  },

  {
    path: "login",
    loadChildren: () =>
      import("./components/main/end-user/login/login.module").then(
        (m) => m.LoginModule
      ),
  },

  {
    path: "home",
    loadChildren: () =>
      import("./components/main/end-user/home/home.module").then(
        (m) => m.HomeModule
      ),
  },

  {
    path: "teachers",
    loadChildren: () =>
      import(
        "./components/main/end-user/dummy-teacher/dummy-teacher.module"
      ).then((m) => m.DummyTeacherModule),
    // canActivate: [AuthGuard],
    // data: {
    //   allowedRole: [RoleTypeSM.ClientAdmin]
    // }
  },
  {
    path: "common",
    loadChildren: () =>
      import(
        "./components/main/end-user/common-converters/common-converters.module"
      ).then((m) => m.CommonConvertersModule),
  },
  {
    path: "engineering",
    loadChildren: () =>
      import(
        "./components/main/end-user/engineering-converters/engineering-converters.module"
      ).then((m) => m.EngineeringConvertersModule),
  },
  {
    path: "favorites",
    loadChildren: () =>
      import("./components/main/end-user/favorites/favorites.module").then(
        (m) => m.FavoritesModule
      ),
  },
  { path: 'others', loadChildren: () => import('./components/main/end-user/others/others.module').then(m => m.OthersModule) },
  {
    path: "**",
    pathMatch: "full",
    loadChildren: () =>
      import("./components/main/not-found/not-found.module").then(
        (m) => m.NotFoundModule
      ),
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
